.header-container {
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: space-around;
  flex-direction: row-reverse;
  align-items: center;
  gap: 5rem;
  background-color: $fifth-color;

  &-user {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5px;
    width: 100%;
    height: 100%;

    p {
      color: white;
      font-size: 1.2rem;
    }

    img {
      border-radius: 50%;
      height: 75%;
    }
  }

  h1 {
    color: $second-color;
    display: flex;
    justify-items: center;
    align-items: center;
    font-size: 5rem;
    height: 100%;
    width: 100%;

    h1 {
      color: $third-color;
    }
  }

  &-links {
    display: flex;
    justify-content: center;
    gap: 1rem;
    width: 100%;

    a {
      text-decoration: none;
      transition: 0.4s;
      font-size: 1.3rem;

    }
   
    &-active {
      color: $second-color;

      &:hover {
        color: $nine-color;
      }
    }

    &-disabled {
      color: $third-color;
     
      &:hover {
        color: $fourth-color;
      }
    }

  }
}
