.profileEdit-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  
  &-form {
    gap:1rem;
    background-color: $fifth-color;
    display: flex;
    flex-direction: column;
    padding: 1rem 1.5rem;
    border-radius: 0.5rem;
    height: 100%;
    width: 25%;
    
    label {
      display: flex;
      justify-content: center;
      flex-direction: column;
      input {
        padding: 0.3rem;
        border-radius: 0.2rem;
      }
      
      textArea {
        border-radius: 0.2rem;
        padding: 0.3rem;
      }


    }
    button {
      color: white;
      width: 100%;
      border-radius: 0.5rem;
      padding: 0.2rem;
      background: $second-color;
      border: none;
    }
  }
}
