.profile-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;

  &-info {
    background-color: $fifth-color;
    display: flex;
    padding: 1rem 2rem;
    flex-direction: column;
    gap: 1rem;
    border-radius: 0.5rem;

    img {
      height: 100%;
      width: 100%;
    }

    p {
      font-size: 1.2rem;
    }

    button {
      color: white;
      width: 100%;
      border-radius: 0.5rem;
      padding: 0.2rem;
      background: $second-color;
      border: none;
    }
  }
}
